<template>
    <div class="detail">
         <van-nav-bar
                title=""
                left-text=""
                @click-left="back"
                left-arrow
         />
         <!--  -->
         <div class="detail_content">

             <!--  -->
              <div class="detail_img">
                  <img class="auto-img" :src="petsData.coverURL"/>
              </div>

              <!--  -->
              <div class="desc_box">
                  <div class="Pets_nameAndLink clearfix">
                      <div class="pets_name fl">
                          <span>{{petsData.name}}</span>
                          <span>({{petsData.engName}})</span>
                      </div>
                      <div class="pets_link fr">
                          <img :src="require('../assets/img/' + (petsData.Collection ? 'like_active.png' : 'like.png'))" @click="myLike(petsData)"/>
                      </div>
                  </div>

                  <!--  -->
                  <div class="pets_information">
                      <div class="pets_features">
                          <div class="pets_information_title"></div>
                          <div class=" clearfix">
                              <span class="pets_features_1 fl"></span>
                               <span class="pets_features_1 fl"></span>
                             
                          </div>
                      </div>
                      <div class="pets_desc">
                          <div class="pets_information_title"></div>
                          <div class="pets_desc_content clearfix">
                              <div class="pets_from" v-for="(item,index) in mainData" :key="index">
                                  <span>{{item.dataName}}</span>
                                  <span>{{item.data}}</span>
                              </div>
                              <!-- <div class="pets_from">
                                  <span></span>
                                  <span></span>
                              </div>
                               <div class="pets_from">
                                  <span></span>
                                  <span>9-15</span>
                              </div>
                              <div class="pets_from">
                                  <span></span>
                                  <span>2000-4000</span>
                              </div> -->
                          </div>
                          <div class="pets_desc_sum">
                              <div class="pets_desc_title" v-for="(item,index) in descData" :key="index">
                                  <span class="pets_desc_title_1">{{item.dataName}}</span>
                                  <span class="pets_desc_title_2">{{item.data}}</span>
                              </div>
                           
                          </div>
                      </div>
                  </div>

              </div>
         </div>
    </div>
</template>

<script>
import {createNamespacedHelpers} from 'vuex'

const {mapState, mapMutations} = createNamespacedHelpers('homeModules')

    export default {
        data(){
            return {
                name: null,
                listType: null,
                titleStyle: "",
                mainData:[],
                descData:[],
                petsData: {},
                saveAddNum: null,
                //myLikeData
                addIndex: null
            }
        },
        computed:{
             ...mapState(['petsStyleData','type','mySearchData','myLikeData'])
        },
        created(){
            //
            this.name = this.$route.query.name;
            this.listType=this.$route.query.type;
            this.titleStyle=this.$route.query.styleName;
            this.changepetsData();
        },
        methods: {
            ...mapMutations(['addmyLikeData','changeLike','removemyLikeData']),
            // 
            back(){
                this.$router.go(-1);
            },
            changepetsData(){
                let obj={};
                let addNum=0;
                let petsSumData=null;
                   //
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);

                if(this.titleStyle=="mySearchData"){
                     addNum=0;
                     let petsSearchData = localSData['mySearchData'].length!=0?localSData['mySearchData']:this.mySearchData;
                     petsSearchData.forEach(v=>{
                        if(v.name==this.name){
                            obj=v;
                            this.saveAddNum=addNum;
                        }
                         addNum++;
                    })
                }else{
                    addNum=0;
                     let petsSumData = localSData['petsStyleData'][this.listType].petsMaxData.length!=0?localSData['petsStyleData']:Object.assign({},this.petsStyleData);
                    // petsSumData = ;
                    petsSumData[this.listType].petsMaxData.forEach(v=>{
                        if(v.name==this.name){
                            obj=v;
                            this.saveAddNum=addNum;
                        }
                        addNum++;
                    })
                }
                
                
               
                // this.petsData=obj;
                obj.characters.split('');
                this.petsData=obj;
                // this.mainData.push({"nationName": '',"nation": this.petsData.nation});
                let arr=['','','',''];
                let arr1=['','','','','']
                let add=0;
                let add1=0;
                for(let pro in this.petsData){
                    if(pro=='nation' || pro=='easyOfDisease'||pro=='life'||pro=='price'){
                        if(this.petsData[pro]!=''){
                             this.mainData.push({dataName:arr[add],data:this.petsData[pro]});
                        }
                        add++;
                       
                    }

                    if(pro=='desc' || pro=='feature'||pro=='characterFeature'||pro=='careKnowledge'||pro=='feedPoints'){
                        if(this.petsData[pro]!=''){
                            this.descData.push({dataName:arr1[add1],data:this.petsData[pro]});
                            
                        }
                        add1++;
                       
                    }
                }
            },
            myLike(item){
                  //
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);
                if(localStorage.getItem('__Tk')==null){
                    this.$dialog.confirm({
                        title: '',
                        message: '',
                    })
                    .then(() => {
                        this.$router.push({name: "Login"})
                    })
                    .catch(() => {
                    });
                    return;
                }
                //petsMaxData
                
                //  let like= state[data.type][data.num].petsMaxData[data.listNum].Collection;
               
                if(!this.petsData.Collection){
                    if(this.titleStyle=='petsStyleData'){
                        
                       this.changeLike({type:this.titleStyle,num:this.listType,listNum:this.saveAddNum}); 
                       
                    //    localSData
                        
                    }else if(this.titleStyle=='mySearchData'){
                        
                        let addNM=this.mySearchData;
                       this.changeLike({type:this.titleStyle,num:this.listType});
                    }
                    
                    this.addmyLikeData({obj:this.petsData,add:this.saveAddNum,listT:this.listType});
                  
                }else{
                    let addnum=0;
                    this.myLikeData.forEach(v=>{
                        if(v.name==this.name){
                            this.addIndex=addnum;
                        }
                        addnum++;
                    })
                     this.changeLike({type:this.titleStyle,num:this.listType,listNum:this.saveAddNum}); 
                    this.removemyLikeData(this.addIndex);
                    
                }
                //  localStorage.setItem('pestData',JSON.stringify(localSData));
                this.petsData.Collection=!this.petsData.Collection;
                
               
            }
        }
    }
</script>

<style lang="less" scoped>
    .detail_content{
        position: fixed;
        top: 46px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        padding: 10px;
        overflow-y: auto;

       .Pets_nameAndLink{
           padding-top: 10px;
           height: 32px;
           line-height: 32px;
       }
       .pets_name{
           font-size: 16px;
           font-weight: 500;
       }
    }
    .pets_features{
        padding: 10px 0px;
        border-bottom: 1px solid #d0d0d0;

        .pets_features_1{
             padding: 5px;
             display: block;
             margin: 5px;
             border-radius: 16px;
             color: #fff;
             background-color: skyblue;
        }
    }
    .pets_information_title{
        padding: 5px 0px;
        font-size: 14px;
        text-align: center;
    }
    .pets_desc_content{
        .pets_from{
            float: left;
            margin: 6px;
            padding: 5px;
            color: #fff;
            border-radius: 17px;
            background-color: skyblue;
        }
    }
    .pets_desc_sum{
        padding-top: 10px;
    }
    .pets_desc_title{
        font-size: 13px;
        // line-height: 5px;
         letter-spacing:3px;
        padding-bottom: 8px;


        .pets_desc_title_1{
            font-weight: 900;
        }
        .pets_desc_title_2{
            line-height: 2;
        }
    }
</style>